import { IPermissionsModel } from '../../../shared/fleio-api/base-model/IPermissionsModel';
import { IIcon } from '../../../shared/ui/common/interfaces/icon';
import { ITitle } from '../../../shared/ui/objects-view/interfaces/card-data/card-title';
import { IObjectStatus, StatusType, StatusValue } from '../../../shared/ui/objects-view/interfaces/object-status';
import { ObjectUIServiceBase } from '../../../shared/ui/objects-view/object-ui-service-base';
import { IAction } from '../../../shared/ui/objects-view/interfaces/actions/action';
import { RouterLinkAction } from '../../../shared/ui/objects-view/actions/router-link-action';
import { Router } from '@angular/router';
import { ConfigService } from '../../../shared/config/config.service';
import { IDataField } from '../../../shared/ui/objects-view/interfaces/card-data/data-field';
import { ApiCallAction, CallType } from '../../../shared/ui/objects-view/actions/api-call-action';
import { IDetailsTab } from '../../../shared/ui/objects-view/interfaces/details/details-tab';
import { DatePipe } from '@angular/common';
import { CallbackAction } from '../../../shared/ui/objects-view/actions/callback-action';
import { VolumesApiService } from '../../../shared/fleio-api/cloud/volume/volumes-api.service';
import { IVolumeModel } from '../../../shared/fleio-api/cloud/model/volume.model';
import { VolumeDetailsOverviewComponent } from './tabs/volume-details-overview/volume-details-overview.component';
import { VolumeEditFormComponent } from './tabs/volume-edit-form/volume-edit-form.component';
import { map } from 'rxjs/operators';
import { IActionResult } from '../../../shared/ui/objects-view/interfaces/actions/action-result';
import { MatDialog } from '@angular/material/dialog';
import { VolumeRenameComponent } from './dialogs/volume-rename/volume-rename.component';
import { VolumeExtendComponent } from './dialogs/volume-extend/volume-extend.component';

export class VolumeUiService extends ObjectUIServiceBase<IVolumeModel> {
  private readonly router: Router;
  private readonly config: ConfigService;
  private readonly volumesApiService: VolumesApiService;
  private readonly datePipe: DatePipe;
  private readonly matDialog: MatDialog;


  constructor(
    volume: IVolumeModel, permissions: IPermissionsModel, state: string,
    router: Router, config: ConfigService, volumesApiService: VolumesApiService, matDialog: MatDialog,
  ) {
    super(volume, permissions, state);
    this.matDialog = matDialog;
    this.router = router;
    this.config = config;
    this.volumesApiService = volumesApiService;
    this.datePipe = new DatePipe(this.config.locale);
  }

  getIcon(): IIcon {
    return null;
  }

  getStatus(): IObjectStatus {
    switch (this.object.status) {
      case 'error':
        return {
          value: StatusValue.Error,
          type: StatusType.Defined,
        };
      case 'deleting':
        return {
          value: StatusValue.Error,
          type: StatusType.Changing,
        };
      case 'in-use':
        return {
          value: StatusValue.Warning,
          type: StatusType.Defined,
        };
      case 'extending':
      case 'attaching':
        return {
          value: StatusValue.Warning,
          type: StatusType.Changing,
        };
      case 'available':
      default:
        return {
          value: StatusValue.Enabled,
          type: StatusType.Defined,
        };
    }
  }

  getTitle(): ITitle {
    switch (this.state) {
      case 'edit':
        return {
          text: `Edit volume ${this.object.name || this.object.id}`,
        };

      case 'create':
        return {
          text: `Create new volume`,
        };

      default:
        return {
          text: `${this.object.name || this.object.id}`,
          subText: this.object.status.toLocaleUpperCase(),
        };
    }
  }

  getActions(): IAction[] {
    const actions: IAction[] = [];

    actions.push(new CallbackAction(
      {
        object: this.object,
        icon: {name: 'edit'},
        tooltip: 'Rename volume',
        name: 'Rename',
        callback: () => {
          return this.matDialog.open(
            VolumeRenameComponent, {
              data: {volume: this.object}
            }).afterClosed().pipe(map(result => {
              if (result === false) {
                return;
              }
              this.router.navigateByUrl(
                this.config.getPanelUrl('cloud/volumes')
              ).catch();
              return {message: result} as IActionResult;
            }));
        }
      }
    ));

    actions.push(new CallbackAction(
      {
        object: this.object,
        icon: {name: 'resize', class: 'fl-icons'},
        tooltip: 'Extend volume',
        name: 'Extend',
        callback: () => {
          return this.matDialog.open(
            VolumeExtendComponent, {
              data: {volume: this.object}
            }).afterClosed().pipe(map(result => {
              if (result === false) {
                return;
              }
              this.router.navigateByUrl(
                this.config.getPanelUrl('cloud/volumes')
              ).catch();
              return {message: result} as IActionResult;
            }));
        }
      }
    ));

    actions.push(new ApiCallAction(
      {
        object: this.object,
        icon: {name: 'delete'},
        name: 'Delete',
        tooltip: 'Delete',
        confirmOptions: {
          confirm: true,
          title: 'Delete volume',
          message: `Are you sure you want to delete volume ${this.object.name}`,
        },
        apiService: this.volumesApiService,
        callType: CallType.Delete,
      }
    ));

    return actions;
  }

  getDetailsActions(): IAction[] {
    const actions = [];

    switch (this.state) {
      case 'create':
        actions.push(new RouterLinkAction({
            name: 'Cancel',
            routerUrl: this.config.getPrevUrl(`cloud/volumes`),
            router: this.router,
          }
        ));
        actions.push(new CallbackAction({name: 'Create'}));
        break;
      case 'edit':
        actions.push(new RouterLinkAction({
            name: 'Cancel',
            routerUrl: this.config.getPrevUrl(`cloud/volumes`),
            router: this.router,
          }
        ));
        actions.push(new CallbackAction({name: 'Save'}));
        break;
      default:
        break;
    }

    return actions;
  }

  getDetailsLink(): string {
    return this.config.getPanelUrl(`cloud/volumes/${this.object.id}`);
  }

  getCardFields(): IDataField[] {
    const fields = [
      {
        value: `${this.object.size} GB, ${this.object.region}`
      },
      {
        name: 'Client',
        value: this.object.client.name,
      },
      {
        value: this.object.type,
      }
    ];

    return fields;
  }

  getTabs(): IDetailsTab[] {
    switch (this.state) {
      case 'details':
        return [
          {
            tabName: 'Overview',
            component: VolumeDetailsOverviewComponent,
          },
        ];
      case 'create':
        return [
          {
            tabName: 'Create',
            component: VolumeEditFormComponent,
          },
        ];
      case 'edit':
        return [
          {
            tabName: 'Create',
            component: VolumeEditFormComponent,
          },
        ];
    }
  }

  getCardTags(): string[] {
    const tags: string[] = [];
    return tags;
  }
}
