import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { IRouteConfig } from '../../../shared/ui-api/interfaces/route-config/route-config';
import { VolumeListComponent } from './volume-list/volume-list.component';
import { VolumeCreateComponent } from './volume-create/volume-create.component';
import { VolumeDetailsComponent } from './volume-details/volume-details.component';
import { VolumeEditComponent } from './volume-edit/volume-edit.component';
import { VolumeResolver } from '../../../shared/fleio-api/cloud/volume/volume.resolver';
import { VolumePermissionsResolver } from '../../../shared/fleio-api/cloud/volume/volume-permissions.resolver';
import { VolumeListResolver } from '../../../shared/fleio-api/cloud/volume/volume-list.resolver';
import { VolumeCreateOptionsResolver } from '../../../shared/fleio-api/cloud/volume/volume-create-options.resolver';
import { FleioObjectsList } from '../../../shared/fleio-api/fleio-objects-list';
import { IBaseFleioObjectModel } from '../../../shared/fleio-api/base-model/base-fleio-object.model';
import { OrderingDirection } from '../../../shared/ui-api/interfaces/route-config/ordering-directions';
import { AuthGuard } from '../../../shared/auth/auth.guard';

const routes: Routes = [
  {
    path: '',
    component: VolumeListComponent,
    resolve: {
      volumes: VolumeListResolver,
    },
    canActivate: [AuthGuard],
    data: {
      config: {
        feature: 'openstack.volumes',
        search: {
          show: true,
          placeholder: 'Search volumes ...',
        },
        subheader: {
          objectNamePlural: 'volumes',
          objectName: 'volume',
          objectList(data): FleioObjectsList<IBaseFleioObjectModel> {
            return data.volumes;
          }
        },
        ordering: {
          default: {
            display: 'Date created',
            field: 'created_at',
            direction: OrderingDirection.Descending,
          },
          options: [
            {display: 'Name', field: 'name'},
            {display: 'Status', field: 'status'},
            {display: 'Size', field: 'size'},
            {display: 'Date created', field: 'created_at'},
          ]
        }
      } as IRouteConfig,
    },
    runGuardsAndResolvers: 'always'
  },
  {
    path: 'create',
    component: VolumeCreateComponent,
    resolve: {
      createOptions: VolumeCreateOptionsResolver,
    },
    data: {
      config: {
        getBreadCrumbDetail: () => {
          return 'Create volume';
        },
      } as IRouteConfig,
    }
  },
  {
    path: ':id',
    component: VolumeDetailsComponent,
    resolve: {
      volume: VolumeResolver,
      permissions: VolumePermissionsResolver,
    },
    data: {
      config: {
        getBreadCrumbDetail: (data) => {
          return data.volume.name;
        },
      } as IRouteConfig,
    },
    runGuardsAndResolvers: 'always',
  },
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule],
})
export class VolumesRoutingModule {
}
